<template>
	<view class="bigboxxs">
		<live-player id="myVideo" :src="videoUrl" autoplay mode="RTC" @fullscreenchange="fullscreenchange" @statechange="statechange" @error="videoErrorCallback" @click="clk">
			<cover-image class="isPosterImg" v-show="isPoster" :src="detailData.logoUrl" ></cover-image>
			<!--顶部栏 竖屏-->
			<cover-view class="video-control">
				<cover-view class="video-control-back" @tap.native.stop="backup"><cover-image src="../../static/img/news/back.png"></cover-image></cover-view>
				<cover-view class="video-control-text" @tap.native.stop="backup">{{ detailData.date }}</cover-view>
				<cover-view class="video-control-more" @tap="choosed">
					<cover-view v-if="isMenu1">
						<cover-image src="../../static/img/news/menu@2x.png"></cover-image>
						<cover-view class="video-control-text1">视频选择</cover-view>
					</cover-view>
					 
					<cover-view v-else></cover-view>
				</cover-view>
			</cover-view>
			<!-- 一键求助 -->
			<cover-view class="sos-box" @tap="sosPopup"><cover-image class="sos-img" src="../../static/img/news/sos.png"></cover-image></cover-view>
			<!-- 位置 -->
			<!-- <cover-view class="adress-box">
				<cover-image class="adress-img" src="../../static/img/news/location.png"></cover-image>
				<cover-view class="adresss">{{ detailData.address }}</cover-view>
			</cover-view> -->
			<!-- 右侧导航栏 -->
			<cover-view class="multi-list rate" :class="{ active: !isMenu1, active1: !isIos && !isMenu1 }" @tap.native.stop>
				<cover-view class="top-close" @tap="closeedMenu">
					<cover-image class="close-img" src="../../static/img/news/close@2x.png"></cover-image>
					<cover-view class="closeds">关闭</cover-view>
				</cover-view>
				<cover-view class="blaks"></cover-view>
				<cover-view class="list-box" scroll-top="50">
					<cover-view v-for="(item, index) in list" :key="index" class="multi-item rate" @tap="switchRate(item)" :class="{ active: item.id == this.detailData.id }">
						{{ item.address }}
					</cover-view>
				</cover-view>
			</cover-view>
		</live-player>

		<uni-popup ref="showtip1" type="center" :mask-click="false">
			<view class="uni-tip1">
				<text class="uni-tip-title1">一键求助</text>
				<textarea class="popup_textarea uni-input" :show-confirm-bar="flase" cursor-spacing="0" placeholder="输入内容..." v-model="feedbackContent"></textarea>
				<view class="uni-tip-group-button1">
					<text class="uni-tip-buttons uni-tip-button11 " @click="cancel('tip')">关闭</text>
					<text class="uni-tip-buttons" @click="submitHelp">提交</text>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			src: 'rtmp://58.200.131.2:1935/livetv/hunantv',
			list: [],
			currentRate: 1, //当前播放id
			rateShow: false, //右侧弹出选项
			detailData: {},
			isMenu: false,
			isshow: false,
			isMenu1: true,
			isIos: true,
			feedbackContent: '',
			isPoster:true,
			videoUrl:''
		};
	},
	onLoad(options) {
		this.detailData = JSON.parse(options.data).item;
		console.log('this.detailData');
		console.log(this.detailData);
		//获取视频接口
		this.geturl()
		//获取视频列表
		this.loadNewsList();
		
	},
	mounted(){
		uni.showLoading({
			title: 'loading'
		});
	},
	onReady: function() {
		this.videoContext = uni.createLivePlayerContext('myVideo', this);
		this.videoContext.play();
	},
	methods: {
		geturl(){
			var obj = { id: this.detailData.id,playrealUrl:""};
			
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x-hqsp.htm', { params: obj })
				.then(res => {
					console.log('视频url');
					console.log(res);
					if (res.data.msgState == 1) {
						
						this.videoUrl = res.data.rtmpSrc;
						// console.log(this.list);
					} else {
						this.videoContext.stop();
						uni.showToast({
							icon: 'none',
							title: '视频已下线！',
							duration: 2000
						});
						setTimeout(()=>{
							this.backup()
						},2000)
						
					}
				})
				.catch(err => {
					console.log(err);
					uni.showToast({
						icon: 'none',
						title: '获取视频失败！',
						duration: 2000
					});
				});
		},
		fullscreenchange(event) {
			console.log(event.detail.fullScreen);

			// 	if (!event.detail.fullScreen) {
			// 		this.backup()
			// 	}
		},
		statechange(e) {
			
			console.log('live-player code:', e.detail.code);
			if (e.detail.code == 2001) {
				uni.showLoading({
					title: 'loading'
				});
			}
			if (e.detail.code == 2003) {
				this.isPoster=false;
				uni.hideLoading();
				this.videoContext.requestFullScreen({ direction: 90 });
			}
			if (e.detail.code == 2105) {
				this.isPoster=false;
				uni.hideLoading();
				//this.videoContext.requestFullScreen({ direction: 90 });
			}
			if (e.detail.code == -2301) {
				uni.showToast({
					icon: 'none',
					title: '网络断连，请重新退出连接！',
					duration: 2000
				});
			}
			//uni.hideLoading();
		},
		backup() {
			uni.navigateBack({
				delta: 1
			});
		},
		clk() {
			let that = this;
			that.isMenu = !that.isMenu;
		},
		loadNewsList() {
			console.log(this.detailData);
			var obj = { pageNo: 1, pageCount: 1000 };

			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x.htm', { params: obj })
				.then(res => {
					//console.log('监控列表数据');
					//console.log(res);
					if (res.data.msgState == 1) {
						this.list = res.data.list;
						console.log(this.list);
					} else {
						uni.showToast({
							icon: 'none',
							title: '监控列表获取失败！',
							duration: 2000
						});
					}
				})
				.catch(err => {
					console.log(err);
					uni.showToast({
						icon: 'none',
						title: '监控列表获取失败！',
						duration: 2000
					});
				});
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.detail.errMsg,
				showCancel: false
			});
		},
		//一键求助弹出框

		sosPopup() {
			console.log('我要反馈');
			this.videoContext.exitFullScreen();
			this.$nextTick(() => {
				this.$refs.showtip1.open();
				this.isshow = true;
				this.isMenu1 = true;
			});
		},
		cancel(type) {
			this.$refs.showtip1.close();
			this.videoContext.requestFullScreen({ direction: 90 });
			this.isshow = false;
		},
		//一键求助
		submitHelp() {
			if(this.feedbackContent==''){
				uni.showToast({
				    title: '请输入求助内容',
				    duration: 2000,
					icon:"none"
				});
				return
			}
			var obj = { note: this.feedbackContent, id: this.detailData.id };
			//发起请求
			console.log('一键求助');
			uni.showLoading({
				title: 'loading'
			});
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-x.htm', obj)
				.then(res => {
					uni.hideLoading();
					console.log(res.data);
					if (res.data.msgState == 1) {
						uni.showToast({
							 title: res.data.msg,
							 duration: 2000,
							
						})
					}else{
						uni.showToast({
							 title: res.data.msg,
							 duration: 2000,
							 icon:'none'
						})
					}
				
					setTimeout(() => {
						this.cancel();
					}, 1000);
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
		},
		//侧边导航栏
		choosed() {
			this.isMenu1 = false;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.isIos = false;
				this.videoContext.exitFullScreen();
			}
		},
		closeedMenu() {
			this.isMenu1 = true;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.videoContext.requestFullScreen({direction:90});
			}
		},
		//选择视频播放
		switchRate(val) {
			let that = this;
			
		this.$nextTick(() => {
			that.detailData = val;
			that.isPoster=true;
			that.geturl()
		});
			
			uni.showLoading({
				title: 'loading'
			});
			this.isMenu1 = true;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.videoContext.requestFullScreen({direction:90});
			}
		}
	}
};
</script>

